import axios from "./axios";

export const getMenu = (prama) => {
  return axios.request({
    url: "/promission/getMenu",
    method: "post",
    data: prama,
  });
};

export const getData = () => {
  return axios.request({
    url: "/home/getData", //默认get请求
  });
};

export const getUser = (params) => {
  return axios.request({
    url: "/user/getUser",
    method: "get",
    params,
  });
};
